.user-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px;
  }
  
  .user-table th,
  .user-table td {
    border: 1px solid #ddd;
    padding: 10px;
    text-align: left;
  }
  
  .user-table th {
    color: white;
  }
  
  .user-table tr:nth-child(even) {
    background-color: #f2f2f2;
  }
  .user-table thead tr{
    border: 3px solid white;
  }